 }


 .App-wrapper {
   width: 100%;
   background-color: rgb(255, 255, 255);
   display: block;
 }

 .App {
   background-color: rgb(255, 255, 255);
   font-family: "Nekst-Bold", "Arial", sans-serif;
   display: block;
 }

 .App-header {
   /* Рассчитайте стоимость автомобиля в лизинг */
   text-align: left;
   width: 753px;
   height: 98px;
   margin: 0;
   padding: 0;
   /* H1 54 */
   font-family: 'Nekst-Black', "Arial", sans-serif;
   font-style: normal;
   font-weight: 900;
   font-size: 54px;
   line-height: 90%;
   /* or 49px */
   color: #111111;
 }




 .App-cardForm {
   margin-bottom: 25px;
 }

 .App-infoCards-Button {
   display: grid;
   grid-template-areas: "sum "
     "payment"
     "button ";
   row-gap: 29px;
 }

 .App-infoCard:first-child {
   grid-area: "sum";
 }

 .App-infoCard:last-child {
   grid-area: "payment";
 }

 .App-button {
   grid-area: "button";
 }










